<template>
  <label class="switch">
    <input class="switch-input" :disabled="disabled" @change="$emit('change', currentValue)" type="checkbox"
           v-model="currentValue">
    <span class="switch-core"></span>
    <div class="switch-label">
      <slot></slot>
    </div>
  </label>
</template>

<script>
  /**
   * mt-switch
   * @module components/switch
   * @desc 切换按钮
   * @param {boolean} [value] - 绑定值，支持双向绑定
   * @param {slot} - 显示内容
   *
   * @example
   * <mt-switch v-model="value"></mt-switch>
   */
  export default {
    name: 'switch-btn',

    props: {
      value: Boolean,
      disabled: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      currentValue: {
        get() {
          return this.value;
        },
        set(val) {
          this.$emit('input', val);
        }
      }
    }
  };
</script>

<style lang="css" lang="scss">
  .switch {
    display: flex;
    align-items: center;
    position: relative;

    * {
      pointer-events: none;
    }
    * {
      touch-action: none;
    }
    .switch-label {
      margin-left: 10px;
      display: inline-block;

      &:empty {
        margin-left: 0;
      }
    }

    .switch-core {
      display: inline-block;
      position: relative;
      height: 64px;
      width: 136px;
      border: 2px solid #dcdcdc;
      border-radius: 32px;
      box-sizing: border-box;
      background: #fff;

        &::after, &::before {
          content: " ";
          position: absolute;
          left: 0;
          top: -2px;
          transition: transform .3s;
          border-radius: 30px;
        }

        &::after {
          height: 60px;
          width: 60px;
          background-color: #fff;
          box-shadow: 0px 7px 16px 0px rgba(197, 20, 16, 0.21);
        }

        &::before {
          height: 58px;
          width: 130px;
          background-color: #f9f9f9;
        }
      }


    .switch-input {
      display: none;

      &:checked {
        + .switch-core {
          border-color: #db3d37;
          background-color: #ec4f49;

          &::before {
            transform: scale(0);
          }

          &::after {
            transform: translateX(71px);
          }
        }
      }
    }
  }
</style>
