<template>
  <Picker
    class="picker-box"
    :slots="slots"
    :itemHeight="40"
    :visibleItemCount="1"
    defaultIndex="2,30"
    @change="onValuesChange"
  />
</template>

<script>
import { Picker } from "mint-ui";
import { add0 } from "../../utils/tools";
import { punchinLimitTime } from "../../utils/config";
  const [[startHour, startMin],[endHour, endMin]] = punchinLimitTime
export default {
  name: "clock-picker",
  components: { Picker },
  props: {
    startTime: {
      type: [Number, String],
      default: 4
    },
    endTime: {
      type: [Number, String],
      default: 12
    },
    defaultValue: {
      type: Array
    }
  },
  data() {
    return {
      slots: [
        {
          flex: 1,
          values: ["04", "05", "06", "07", "08", "09"],
          className: "picker-left",
          textAlign: "center",
          defaultIndex: 0
        },
        {
          divider: true,
          content: ":",
          className: "picker-center"
        },
        {
          flex: 1,
          values: ["04", "05", "06", "07", "08", "09"],
          className: "picker-right",
          textAlign: "center",
          defaultIndex: 0
        }
      ]
    };
  },
  methods: {
    init() {
      // 设定时间范围
      const { startTime, endTime } = this;
      const leftValues = [];
      for (let i = startTime; i < endTime; i++) {
        leftValues.push(add0(i));
      }
      const rightValues = [];
      for (let i = 0; i < 60; i++) {
        rightValues.push(add0(i));
      }
      this.slots[0].values = leftValues;
      this.slots[2].values = rightValues;

      // 初始默认值
      if (this.defaultValue) {
        const defaultValue = this.defaultValue;
        const index1 = this.slots[0].values.indexOf(defaultValue[0]);
        const index2 = this.slots[2].values.indexOf(defaultValue[1]);
        if (~index1) this.slots[0].defaultIndex = index1;
        if (~index2) this.slots[2].defaultIndex = index2;
      }
    },
    onValuesChange(el, val) {
      console.log(val);
      if (val[0] == startHour) {
        const rightValues = [];
        for (let i = startMin; i < 60; i++) {
          rightValues.push(add0(i));
        }
        el.setSlotValues(1, rightValues);
      } else if (val[0] == endHour) {
        const rightValues = [];
        for (let i = 0; i <= endMin; i++) {
          rightValues.push(add0(i));
        }
        el.setSlotValues(1, rightValues);
      } else {
        const rightValues = [];
        for (let i = 0; i < 60; i++) {
          rightValues.push(add0(i));
        }
        el.setSlotValues(1, rightValues);
      }
      this.$emit("input", val);
    }
  },
  created() {
    this.init();
  }
};
</script>

