<template>
  <div class="set-time-wrap flex" v-if="dialogShow">
    <div class="set-box">
      <div class="set-box-title">设置时间</div>
      <div class="set-time-tip">
        请滑动选择时间
      </div>
      <ClockPicker :itemHeight="40"
                   :default-value="defaultValue"
                   :visibleItemCount="1"
                   v-model="setTime"/>
      <div class="time-limit">打卡时间 {{punchinLimit}}</div>
      <div class="btn-box">
        <span @click="cancel">取消</span>
        <span @click="ack">确定</span>
      </div>
    </div>

  </div>
</template>

<script>

  import ClockPicker from '../../components/ClockPicker/ClockPicker';
  import {punchinLimitTime} from "../../utils/config";
  const [[startHour, startMin],[endHour, endMin]] = punchinLimitTime
  export default {
    name: "set-time",
    components: {ClockPicker},
    props: {
      dialogShow: {
        type: Boolean,
        default: false
      },
      defaultValue: {
        type: Array,
      }
    },
    data() {
      return {
        setTime: ['08', '00'],
        punchinLimit: `${startHour}:${startMin} - ${endHour}:${endMin}`,
        // setTime: this.value,
      }
    },
    methods: {
      cancel() {
        this.$emit('update:dialogShow',false)
      },
      ack() {
        this.$emit('input',this.setTime)
        this.cancel()
      }
    },
  }
</script>

