<template>
  <div class="remind-wrap wrap flex-column">
    <div class="top-bg"></div>
    <div class="top-switch flex">
      <span>开启每日早安提醒</span>
      <SwitchBtn v-model="remindFlag"></SwitchBtn>
    </div>

    <div class="set-cont">
      <div v-if="!remindFlag" class="tip-close-box flex-column">
        <img src="./imgs/bg-tipclose.png" alt="">
        <span>打卡提醒已关闭</span>
        <span>小v不能每天提醒你了呦</span>
      </div>

      <div v-else class="tip-set-box flex-column">
        <div class="set-title">提醒方式</div>
        <!--模式图标切换-->
        <div class="set-type-wrap flex">
          <div class="type-item flex-column"
               :class="{'active':remindType==='0'}"
               @click="changeType('0')">
            <img :src="remindType==='0'?imgs.zhinengA:imgs.zhineng" alt="">
            <span class="text-tip">小v智能提醒</span>
            <span class="text-flag-default"></span>
            <ButtonM class="text-flag-active">当前</ButtonM>
          </div>
          <div class="type-item flex-column"
               :class="{'active':remindType!=='0'}"
               @click="changeType('1')">
            <img :src="remindType==='0'?imgs.clockImg:imgs.clockImgA" alt="">
            <span class="text-tip">自定义提醒</span>
            <span class="text-flag-default"></span>
            <ButtonM class="text-flag-active">当前</ButtonM>
          </div>
        </div>

        <template v-if="remindType==='0'">
          <div class="tip-text">小v会根据你最近的平均打卡时间</div>
          <div class="tip-text">发送一条消息提醒你打卡呦</div>
        </template>
        <template v-else>
          <div class="set-time flex">
            <span>设定早起提醒时间</span>
            <span @click="toSetTime">{{remindTime.join(':')}}</span>
            <img src="./imgs/icon-arrow.png" @click="toSetTime">
          </div>

          <div class="tip-text">小v会在你设定的时间</div>
          <div class="tip-text">发送一条消息提醒你打卡呦</div>
        </template>

        <ButtonM class="save-btn" @click="saveSet">保存</ButtonM>
      </div>
    </div>
    <SetTime :dialogShow.sync="dialogShow"
             :default-value="remindTime"
             v-model="remindTime"/>

  </div>
</template>

<script>
  import SwitchBtn from '../../components/SwitchBtn/SwitchBtn';
  import ButtonM from '../../components/ButtonM/ButtonM';
  import SetTime from '../../components/ClockPicker/SetTime';
  import mixin from './components/mixin';
  import {changeRemind, clog} from "../../api/punchin";
  import { Indicator, Toast } from 'mint-ui';
  import {title_tip} from "../../utils/publictip";
  export default {
    name: "remind",
    components: {SwitchBtn, SetTime, ButtonM},
    mixins: [mixin],
    data() {
      return {
        remindFlag: false,
        remindType: '0',  //0系统智能提醒、1自定义提醒"
        dialogShow: false,
        remindTime: ['06', '00'],
        imgs: {
          zhineng: require('./imgs/icon-zhineng-default.png'),
          zhinengA: require('./imgs/icon-zhineng-active.png'),
          clockImg: require('./imgs/icon-cloak-default.png'),
          clockImgA: require('./imgs/icon-clock-active.png'),
        },
      }
    },
    methods: {
      init2() {
        this.initPromise.then(res => {
          const {remindFlag, remindType, remindTime} = this.pinchinInfo
          this.remindFlag = !!(+remindFlag)
          this.remindType = remindType
          this.remindTime = remindTime ? remindTime.split(':') : this.remindTime
        })
      },
      changeType(val) {
        if(val==0){
          _hmt.push(['_trackEvent', 'click', '提醒打卡页', '选择小V智能提醒']);
        }else{
          _hmt.push(['_trackEvent', 'click', '提醒打卡页', '选择自定义提醒']);
        }
        this.remindType = val
      },
      toSetTime() {
        this.dialogShow = true
      },
      saveSet() {
        const data = {
          remindFlag: Number(this.remindFlag) + '',
        }
        if(data.remindFlag === '1') {
          data.remindType = this.remindType
          if(data.remindType === '1') {
            data.remindTime = this.remindTime.join(':')
          } else if(data.remindType === '' || data.remindType === null) { // 有些用户默认为'', 会报参数错误
            data.remindType = '1'
            data.remindTime = this.remindTime.join(':')
          }
        }
        Indicator.open('保存中')
        changeRemind(data).then(res => {
          Indicator.close()
          if(res.result.businessCode === '0') {
            this.setPinchinInfo(data)
            Toast({
              message: '保存成功',
              position: 'bottom',
              duration: 2000
            });
          } else {
            title_tip('提 示', res.result.msg, '我知道了');
          }

        })
        clog('早安打卡', ['vjf-h5-log','transTime','openid','好物好生活平',location.href.split('?')[0],'设置打卡提醒','保存设置的打卡提醒']);
        _hmt.push(['_trackEvent', 'click', '提醒打卡页', '保存打卡提醒时间']);
      }
    },
    watch: {
      remindFlag(newVal, oldVal){
        if(!newVal && oldVal) {
          this.saveSet()
          _hmt.push(['_trackEvent', 'click', '提醒打卡页', '关闭打卡提醒']);
        }else{
          _hmt.push(['_trackEvent', 'click', '提醒打卡页', '打开打卡提醒']);
        }
      }
    },
    created() {
      this.init2()
      console.log(this.initPromise);
      clog('早安打卡', ['vjf-h5-log','transTime','openid','好物好生活平',location.href.split('?')[0],'进入设置打卡提醒页','进入设置打卡提醒页']);
      _hmt.push(['_trackEvent', 'click', '首页', '点击进入提醒打卡页面']);
    }
  }
</script>

